@import "../../variables.scss";

header {
  padding:0 100px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  @media (max-width: $breakpoint-mobile) {
    padding: 0 20px;
  }

  a{
    text-decoration: none;
  }

  h1 {
    font-size: 48px;
    color: $primary-color;

    @media (max-width: $breakpoint-mobile) {
      width: 145px;
      height: 48px;
      margin-left: 0;
    }

    @media (max-width: $breakpoint-xxs) {
      width: 100px;
      height: 33px;
    }
  }
}



