@import "../../variables.scss";

article {
  background-color: $primary-color;
  color: black;
  width: 280px;
  text-align: center;
  padding: 15px;
  border-radius: 10px;
  height: 150px;

  * {
    margin: 0;
  }

  h2 {
    font-size: 20px;
    font-weight: bold;
  }
  h3 {
    margin-top: 10px;
    font-size: 16px;
    font-style: italic;
  }

  .classId {
    font-size: 16px;
    font-weight: bold;
    margin: 10px 0;
  }

  .room {
    font-size: 14px;
  }

  .container__room-week {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    .week {
      font-size: 18px;
      font-weight: bold;
      width: 32px;
      height: 32px;
      background-color: white;
      border-radius: 16px;
      align-content: center;
    }
  }
}
