@import "../../variables.scss";

.container__main {
  max-width: 1240px;
  margin: auto;
  padding: 0 100px;

  @media (min-width: $breakpoint-mobile) and (max-width: $breakpoint-desktop) {
    padding: 0 100px;
  }

  @media (max-width: $breakpoint-mobile) {
    padding: 0;
  }
}

.container__day {
  margin: 0 auto;
  padding: 30px;
  background-color: $primary-bg-color;
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  gap: 10px;

  @media (max-width: $breakpoint-mobile) {
    margin: 0 auto;
    padding: 0;
    background-color: white;
    flex-direction: column;
    row-gap: 20px;
  }

  a {
    width: 30%;
    object-fit: cover;

    @media (min-width: $breakpoint-mobile) and (max-width: $breakpoint-desktop) {
      width: 46%;
      gap: 20px;
    }

    @media (max-width: $breakpoint-mobile) {
      width: 100%;
      gap: 20px;
    }
  }
}
